import React from 'react';
import './App.css';
import { BrowserRouter, NavLink, Route } from "react-router-dom";
import Home from './Components/home';
import Computers from './Components/Computers';
import Engniering from "./Components/Engniering";
import Media from './Components/Media';
const HomePage =()=><div className="homePage"><NavLink to="/Home">Start Browsering</NavLink></div>

function App() {
 
  
  return (
    <div className="App">
       <BrowserRouter >
        <NavLink to="/"></NavLink>
        <NavLink to="/Home"></NavLink>
          <NavLink to="/Computers"></NavLink>
           <NavLink to="/Media"></NavLink>
          <NavLink to="/Engniering"></NavLink>
        <Route path="/" exact component={HomePage}></Route>
        <Route path="/Home" exact component={Home}></Route>
          <Route path="/Computers" component={Computers}></Route>
          <Route path="/Media"  component={Media}></Route>
          <Route path="/Engniering"  component={Engniering}></Route>
     </BrowserRouter>
       
     
    </div>
  );
}

export default App;
