import React from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faPhone } from "@fortawesome/free-solid-svg-icons";
import '../App.css';

const Head = () => {
    return (
      <React.Fragment>
        <section className="header-bar text-center">
          <div className="container">
            <div className="row">
              <div className="col-sm">
                <FontAwesomeIcon icon={faPhone} /> 19644
              </div>
              <div className="col-sm">
                <FontAwesomeIcon icon={faEnvelope}  /> info@sha.edu.eg
              </div>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
};
export default Head;
