import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/js/dist/carousel";
import Hie from '../image/hie.png';
import Nav from './navebar';
import Head from "./header";
import Footer from './footer';
import "../App.css";
import { Component } from "react";

class Engniering extends Component {
    render() {
        return (
            <React.Fragment>
                <Head></Head>
                <Nav></Nav>
                <section className="thumneles text-center">
                    <div className="container">
                        <div className="row">

                            <div className="col-lg-12">
                                <div className="thum">
                                    <h1> هندسة</h1>
                                    <p>تنسيق العام الماضي: <span> %88.7</span></p>
                                    <p>مصروفات العام الماضي: <span> 20,100 جنية</span></p>
                                    <p>الدراسة:<span> اللغة العربية+اللغة الانجليزية</span></p>
                                </div>
                            </div>

                            <div className="col-lg-12">
                                <div className="thum">
                                    <h1> الاقسام</h1>
                                </div>
                            </div>
                        </div>
                        <div className="row">

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>الهندسة المدنية</h1>
                                </div>
                            </div>

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>القوي والألات الكهربائية</h1>
                                </div>
                            </div>

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>الهندسة المعمارية</h1>
                                </div>
                            </div>

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>الاتصالات والحاسبات</h1>
                                </div>
                            </div>

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>الهندسة الطبية والمنظومات</h1>
                                </div>
                            </div>

                            <div className="col-lg">
                                <div className="thum">
                                    <img src={Hie} className=" i" alt="..."></img>
                                    <h1>الرياضيات والفزياء الهندسة</h1>
                                </div>
                            </div>

                        </div>
                    </div>
                </section>

                <Footer></Footer>
            </React.Fragment>
        );
    }
};
export default Engniering;